import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import {
  FormGroup,
  FormControl,
  Validators,
  FormBuilder,
} from '@angular/forms';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { Router } from '@angular/router';
import { ToastController, ModalController } from '@ionic/angular';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';
import { BranchComponent } from 'src/app/components/branch/branch.component';
import { TimeSlotsComponent } from 'src/app/components/time-slots/time-slots.component';
import { TransactionDateComponent } from 'src/app/components/transaction-date/transaction-date.component';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ToastService } from 'src/app/services/toast.service';
import { TokenV2Page } from '../../token-v2/token-v2.page';
import { Location } from '@angular/common';

@Component({
  selector: 'app-cheque-deposit-v2',
  templateUrl: './cheque-deposit-v2.page.html',
  styleUrls: ['./cheque-deposit-v2.page.scss'],
})
export class ChequeDepositV2Page implements OnInit {
  public form: FormGroup;
  isShow: boolean = true;
  selectedCountryCode: string;
  currencyControl = new FormControl<string>('');
  accountInfo: any;
  currencies: any;
  transAmount: any;
  chargeAmount: any = 5;
  customerDetails: any;
  homeIconToggle: boolean = false;
  frmToggle: boolean;
  animationState = 'out';
  filteredOptions: Observable<any[]>;
  phoneNumber: string;
  customerId: string;
  routerData: any;
  editMode: boolean = false;
  selectedCurrency: any;
  selectedAccount: any;
  filteredAccount: any = [];
  chequeResponse: any;

  transactionId: any;
  currentUser: any;
  showMore: boolean = false;
  flag: boolean = true;
  icon_arrow_svg: any = 'assets/icon/arrow-down.svg';
  depositType: any;

  @ViewChild(MatAutocompleteTrigger) autocomplete: MatAutocompleteTrigger;

  repFG: FormGroup = this.formBuilder.group({
    personName: ['', Validators.required],
    phoneNo: [
      '',
      [Validators.required, Validators.minLength(10), Validators.maxLength(10)],
    ],
    repdocumentId: [''],
    repdocumentNumber: [''],
  });

  currentBalance: any;
  transAmt: any;
  users: any = [];
  productCode = 'CQD';
  tokenOrigin = 'Mobile';
  accountflag: boolean;

  CHEQUE_TYPES: string[] = ["Bearer - Other Bank Cheque","Traveler","Banker","Bearer - Same Bank Cheque"]
  chequeTypeSelected: any = 'Bearer - Other Bank Cheque';
  loanRepaymentResponse: any;

  constructor(
    private formBuilder: FormBuilder,
    private api: ApiService,
    private router: Router,
    public toastCtrl: ToastController,
    private shareDataService: DataService,
    public loading: LoadingService,
    private cdr: ChangeDetectorRef,
    private modalController: ModalController,
    public toastService: ToastService,
    public datepipe: DatePipe,
    private location: Location
  ) {}

  ngOnInit() {
    this.currentUser = this.shareDataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = this.currentUser?.customerId;
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    this.formInit();
    this.loadData();

    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      console.log('Values?', this.routerData);
      this.editMode = this.routerData?.data ? true : false;
    }

    if (this.editMode) {
      const data = this.routerData?.data;
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === data?.transactionCurrency
      );
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode =
        this.selectedCurrency?.countryCode.toLowerCase();

      let deposit_type =
        data?.representativeInfo.length != 0 ? 'representative' : 'self';
      this.depositType = deposit_type;
      this.form.get('depositType')?.patchValue(deposit_type);

      if (deposit_type == 'representative') {
        this.form.addControl('representativeInfo', this.repFG);
        this.form
          .get('representativeInfo')
          .get('personName')
          .patchValue(data?.representativeInfo?.[0]?.personName);
        this.form
          .get('representativeInfo')
          .get('phoneNo')
          .patchValue(data?.representativeInfo?.[0]?.phoneNo);
        this.form
          .get('representativeInfo')
          .get('repdocumentId')
          .patchValue(data?.representativeInfo?.[0]?.documentId);
        this.form
          .get('representativeInfo')
          .get('repdocumentNumber')
          .patchValue(data?.representativeInfo?.[0]?.documentNumber);
      } else {
        this.form.removeControl('representativeInfo');
      }
    } else {
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === 'INR'
      );
      this.selectedCurrency = countryCode[0];
      this.depositType = 'self';
      this.form.get('depositType')?.patchValue('self');
    }

    this.shareDataService.getAccountInfo.subscribe((data) => {
      this.accountInfo = data;
    });

    this.filteredOptions = this.currencyControl.valueChanges.pipe(
      startWith(''),
      map((value: any) => {
        const currencyCode =
          typeof value === 'string' ? value : value?.currencyCode;
        return currencyCode
          ? this._filter(currencyCode as string)
          : this.currencies.slice();
      })
    );

    let today = new Date().toISOString();
    const convertedDate = this.datepipe.transform(today, 'yyyy-MM-dd');
    this.form.get('transactionDate').patchValue(convertedDate);
    // this.patchScannedData();

    this.location.subscribe(() => {
      this.patchScannedData();
    });
  }

  patchScannedData() {
    let scannedData = JSON.parse(localStorage.getItem('chequeData'));
    console.log(scannedData);
    if (!scannedData) {
      return;
    } else {
      let formValue: any = {};
      formValue.chqAccountNumber = scannedData.accNo;
      formValue.chequeNumber = scannedData.chequeNo;
      formValue.chqAccountBank = scannedData.name;

      this.form.patchValue(formValue);
    }
    // this.api.getBankNameFromIfsc(scannedData.ifsc).subscribe((res) => {
    //   if (res) {
    //     formValue.chqAccountBank = res?.BANK;
    //     this.form.patchValue(formValue);
    //   } else {
    //     this.form.patchValue(formValue);
    //   }
    // });
  }

  onScroll() {
    if (this.autocomplete?.panelOpen) {
      this.autocomplete.updatePosition();
    }
  }

  formInit() {
    this.form = this.formBuilder.group({
      transactionId: [''],
      customerId: [''],
      chequeDepositId: [''],
      productCode: ['CQD', [Validators.required]],
      tokenOrigin: ['Mobile', [Validators.required]],
      accountNumber: ['', [Validators.required]],
      accountBalance: [''],
      accountCurrency: [''],
      transactionCurrency: ['', [Validators.required]],
      transactionAmount: ['', [Validators.required]],
      branchFlag: [true, [Validators.required]],
      accountBranch: [''],
      transactionDate: ['', [Validators.required]],
      transactionBranch: ['', [Validators.required]],
      isMobileTrans: [true, [Validators.required]],
      transactionTime: ['', [Validators.required]],
      exchangeRate: [''],
      accountAmount: [''],
      totalChargeAmount: [this.chargeAmount],
      denomination: [null],
      totalAmount: [''],
      remarks: [''],
      totalTransactionAmount: ['', [Validators.required]],
      chequeType: [''],
      chequeNumber: ['', [Validators.required]],
      chqAccountNumber: ['' , [Validators.required]],
      chqAccountBank: ['' , [Validators.required]],
    });
    this.form.valueChanges.subscribe(()=>{
      this.form.updateValueAndValidity();
      
    })
  }
  get checkInitialPageValidation(){
    
    if(this.form.get("chequeType").value == "Traveler" || this.form.get("chequeType").value == "Banker" ){
      return (this.form.get("chequeNumber").valid && this.form.get("transactionAmount").valid)
    } else if (this.form.get("chequeType").value == "Bearer - Other Bank Cheque"){
      return ((this.form.get("accountNumber").valid  && this.form.get("transactionCurrency").valid && this.form.get("transactionAmount").valid && this.form.get("chequeNumber").valid) &&this.form.get("chqAccountNumber").valid && this.form.get("chqAccountBank").valid )
    } else if (this.form.get("chequeType").value == "Bearer - Same Bank Cheque"){
      return this.form.get("transactionAmount").valid
    }
  }
  check(){
    console.log((this.form));
    
  }
  private _filter(currency: string): any[] {
    const filterValue = currency.toLowerCase();
    return this.currencies.filter((option) =>
      option.currencyCode.toLowerCase().includes(filterValue)
    );
  }

  formatAccountBalance(balance: any) {
    if (balance > 0) {
      let toInt = parseInt(balance);
      let toLocale = toInt.toLocaleString('en-IN', {
        style: 'currency',
        currency: 'INR',
      });
      return toLocale;
    } else {
      let toInt = parseInt('0');
      let toLocale = toInt.toLocaleString('en-IN', {
        style: 'currency',
        currency: 'INR',
      });
      return toLocale;
    }
  }

  balanceCheck(event: any) {
    console.log(( this.form.get("accountNumber").valid  && this.form.get("transactionCurrency").valid && this.form.get("transactionAmount").valid));
    console.log(this.form);
    
    this.transAmt = event.target.value;
    this.transAmt = this.transAmt.toString().replace(/,/g, '');
    this.currentBalance = this.filteredAccount.amount;
    this.updateTransAmount();
    if (parseFloat(this.currentBalance) < parseFloat(this.transAmt)) {
      this.showToast();
    } else {
      return;
    }
  }

  async showToast() {
    const toast = await this.toastCtrl.create({
      message: 'Transaction Amount should not exceed than Account Balance',
      duration: 2000,
      position: 'bottom',
    });
    toast.present();
  }

  loadData() {
    this.loading.present();
    this.api.custpomerDetails(this.phoneNumber).subscribe(
      (resp) => {
        this.loading.dismiss();
        resp.custAccount = resp.custAccount.filter(
          (card) => card.status === 'APPROVED'
        );
        this.customerDetails = resp;
        this.upsertAccount(resp);
      },
      (err: any) => {
        this.loading.dismiss();
      }
    );
  }

  onBlurEvent(e: any) {
    this.updateTransAmount();
  }

  upsertAccount(filteredResponseSavingAccount: any) {
    this.users = filteredResponseSavingAccount.custAccount;
    this.currentBalance = this.users[0].amount;

    this.selectedCountryCode = this.selectedCurrency.countryCode.toLowerCase();
    this.form
      .get('transactionCurrency')
      ?.patchValue(this.selectedCurrency.currencyCode);

    if (this.editMode) {
      const data = this.routerData?.data;
      this.form.get('accountNumber').patchValue(data?.accountNumber);
      this.form.get('chequeNumber').patchValue(data?.chequeNumber);
      this.form.get('transactionAmount').patchValue(data?.transactionAmount);
      this.form.get('remarks').patchValue(data?.remarks);
      this.form.get('transactionBranch').patchValue(data?.transactionBranch);
      this.form.get('transactionDate').patchValue(data?.transactionDate);
      this.form.get('transactionTime').patchValue(data?.transactionTime);
      this.form
        .get('totalTransactionAmount')
        .patchValue(data?.totalTransactionAmount);
    } else {
      if (this.accountInfo.accountId != null) {
        this.form.get('accountNumber').patchValue(this.accountInfo.accountId);
      } else {
        this.form.get('accountNumber').patchValue(this.users[0].accountId);
      }

      this.accountflag = this.users.length == 1 ? true : false;
      this.form.controls.accountBranch.patchValue(
        filteredResponseSavingAccount.custAccount[0].accountBranch
      );
      this.form
        .get('transactionBranch')
        .patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
    }
    this.cdr.markForCheck();
  }

  onAccountSelect(e: any) {
    console.log('selected', this.selectedAccount);
    let filteredAccount: any = this.users.filter(
      (account) => account.accountId === this.selectedAccount
    );
    this.filteredAccount = filteredAccount[0];
    const countryCode = this.currencies.filter(
      (i: any) => i.currencyCode === this.filteredAccount?.accountCurrency
    );
    console.log('countryCode', countryCode);
    this.selectedCurrency = countryCode[0];
    this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();
    this.form
      .get('transactionCurrency')
      ?.patchValue(this.filteredAccount?.accountCurrency);
    this.form
      .get('accountBranch')
      ?.patchValue(this.filteredAccount?.accountBranch);
  }

  chequeTypeChange(event) {
    this.chequeTypeSelected = event.value;
    console.log(this.chequeTypeSelected);
  }

  keyPressNumbers(event) {
    var charCode = event.which ? event.which : event.keyCode;
    // Only Numbers 0-9
    if (charCode < 48 || charCode > 57) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  getExChangeRate(currency) {
    this.api
      .getExchangeRate(this.accountInfo.accountCurrency)
      .subscribe((res: any) => {
        const code = this.currencies.filter(
          (x: any) => x.currencyCode == currency
        );
        let filteredCurrency = code[0].currencyCode;
        const rate = res?.rates?.[filteredCurrency];
        this.form.get('exchangeRate').patchValue(rate);
        let toFix = this.form.get('transactionAmount')?.value;
        let val =
          toFix != 'undefined' || toFix != undefined
            ? toFix?.replace(/,/g, '')
            : 0;
        if (!isNaN(val)) {
          let updatedAmount =
            val * this.form.get('exchangeRate')?.value + this.chargeAmount;
          if (!isNaN(updatedAmount)) {
            let formatedAmount: any = this.shareDataService.formatCurrency(
              updatedAmount,
              this.form.get('transactionCurrency')?.value
            );
            this.form.get('totalTransactionAmount').patchValue(formatedAmount);
            this.cdr.markForCheck();
          }
        }
      });
    this.updateTransAmount();
  }

  updateTransAmount() {
    let toFix = this.form.get('transactionAmount')?.value;
    console.log('check value', toFix);
    let val =
      toFix != 'undefined' || toFix != undefined ? toFix?.replace(/,/g, '') : 0;
    console.log('check value after', val);
    if (!isNaN(val)) {
      let updatedAmount =
        val * this.form.get('exchangeRate')?.value + this.chargeAmount;
      if (!isNaN(updatedAmount)) {
        let formatedAmount: any = this.shareDataService.formatCurrency(
          updatedAmount,
          this.form.get('transactionCurrency')?.value
        );
        this.form.get('totalTransactionAmount').patchValue(formatedAmount);
        this.cdr.markForCheck();
      }
    }
  }

  selectCurrencyCode(e: any) {
    if (
      typeof e == 'undefined' ||
      typeof e == undefined ||
      typeof e == null ||
      typeof e == 'object'
    ) {
      return;
    } else {
      if (e.length > 2) {
        const filteredCurrency = this.currencies?.filter(
          (i: any) => i.currencyCode === e
        );
        this.selectedCurrency = filteredCurrency[0];
        this.selectedCountryCode =
          filteredCurrency[0].countryCode.toLowerCase();
        this.getExChangeRate(filteredCurrency[0].currencyCode);
        this.cdr.markForCheck();
      }
    }
  }

  getHomeBranch() {
    this.homeIconToggle = !this.homeIconToggle;
    if (this.homeIconToggle) {
      this.form
        .get('transactionBranch')
        ?.patchValue(this.users[0]?.accountBranch);
    } else {
      this.form.get('transactionBranch')?.patchValue('');
    }
  }

  datePopup() {
    let oldDate = this.form.get('transactionDate')?.value;
    this.modalController
      .create({
        component: TransactionDateComponent,
        componentProps: {
          date: this.form.get('transactionDate').value,
        },
      })
      .then((modalResp) => {
        modalResp.present();
        modalResp.onDidDismiss().then((res) => {
          console.log(typeof res?.data);
          if (typeof res?.data == 'undefined') {
            this.form.get('transactionDate')?.patchValue(oldDate);
          } else {
            this.form.get('transactionDate')?.patchValue(res.data);
          }
        });
      });
  }

  openPopup() {
    this.modalController
      .create({
        component: TimeSlotsComponent,
        componentProps: {
          date: this.form.get('transactionDate').value,
        },
      })
      .then((modalResp) => {
        modalResp.present();
        modalResp.onDidDismiss().then((res) => {
          if (
            res.data != null ||
            res.data != undefined ||
            res.data != 'undefined'
          ) {
            console.log(res);
            this.form.get('transactionTime').patchValue(res.data?.form);
          }
        });
      });
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: BranchComponent,
      id: 'branchModal',
      componentProps: {},
    });

    modal.onDidDismiss().then((modelData) => {
      if (modelData !== null) {
        this.form
          .get('transactionBranch')
          .patchValue(modelData.data['data'].branchName);
      }
    });

    return await modal.present();
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

  goToFileUpload() {
    this.router.navigate(['file-upload']);
  }

  goToScan() {
    this.router.navigate(['scan']);
  }

  onScreenRefresh() {
    this.editMode = this.routerData?.data ? true : false;
    this.currentUser = this.shareDataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = this.currentUser?.customerId;
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    this.formInit();
    this.loadData();

    if (this.editMode) {
      const data = this.routerData?.data;
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === data?.transactionCurrency
      );
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode =
        this.selectedCurrency?.countryCode.toLowerCase();

      let deposit_type =
        data?.representativeInfo.length != 0 ? 'representative' : 'self';
      this.depositType = deposit_type;
      this.form.get('depositType')?.patchValue(deposit_type);

      console.log('TESTON', data?.representativeInfo?.[0]?.personName);

      if (deposit_type == 'representative') {
        this.form.addControl('representativeInfo', this.repFG);
        this.form
          .get('representativeInfo')
          .get('personName')
          .patchValue(data?.representativeInfo?.[0]?.personName);
        this.form
          .get('representativeInfo')
          .get('phoneNo')
          .patchValue(data?.representativeInfo?.[0]?.phoneNo);
        this.form
          .get('representativeInfo')
          .get('repdocumentId')
          .patchValue(data?.representativeInfo?.[0]?.documentId);
        this.form
          .get('representativeInfo')
          .get('repdocumentNumber')
          .patchValue(data?.representativeInfo?.[0]?.documentNumber);
      } else {
        this.form.removeControl('representativeInfo');
      }
    } else {
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === 'INR'
      );
      this.selectedCurrency = countryCode[0];

      this.depositType = 'self';
      this.form.get('depositType')?.patchValue('self');
    }

    this.shareDataService.getAccountInfo.subscribe((data) => {
      this.accountInfo = data;
    });

    this.form.get('transactionAmount')?.valueChanges.subscribe((val) => {
      if (val) {
        this.updateTransAmount();
      }
    });
  }

  segmentChanged(e: any) {
    if (e == 'representative') {
      this.form.addControl('representativeInfo', this.repFG);
    } else {
      this.form.removeControl('representativeInfo');
    }
    console.log(this.form.value);
  }

  keyPressMob(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  submit(form: any) {
    console.log(form);

    var date = new Date(form.transactionDate);
    let latest_date = this.datepipe.transform(date, 'yyyy-MM-dd');
    form.transactionDate = latest_date;
    form.customerId = this.customerId;
    form.productCode = this.productCode;
    form.accountType = this.filteredAccount?.accountType;
    form.tokenOrigin = this.tokenOrigin;
    form.accountCurrency = this.filteredAccount?.accountCurrency || '';
    form.isMobileTrans = true;
    form.holderName = this.filteredAccount?.customerName;
    form.accountNumber = this.form.value.accountNumber;

    if (this.depositType == 'representative') {
      form.representativeInfo = [
        {
          phoneNo: this.form.get('representativeInfo').get('phoneNo')?.value,
          personName: this.form.get('representativeInfo').get('personName')
            ?.value,
          documentId: this.form.get('representativeInfo').get('repdocumentId')
            ?.value,
          documentNumber: this.form
            .get('representativeInfo')
            .get('repdocumentNumber')?.value,
        },
      ];
    } else {
      form.representativeInfo = [];
    }

    // Total transaction convertion format
    let ttam = this.form.value['totalTransactionAmount'].substring(1);
    form.totalTransactionAmount = ttam.replace(/,/g, '');

    // let modeInfo: any;
    // if (form?.paymentMethod === 'Account') {
    //   modeInfo = [
    //     {
    //       loanAccountNo: form?.loanAccountNumber,
    //       rePaymentMode: form?.paymentMethod,
    //       // category: this.module,
    //       accountNumber: form?.accountPaymentNumber,
    //       // accountCurrency: this.filteredPaymentAccount?.accountCurrency
    //     }
    //   ];
    // } else if (form?.paymentMethod === 'Cheque') {
    //   modeInfo = [
    //     {
    //       loanAccountNo: form?.loanAccountNumber,
    //       rePaymentMode: form?.paymentMethod,
    //       // category: this.module,
    //       // accountCurrency: this.filteredPaymentAccount?.accountCurrency,
    //       chequeType: form?.chequeType,
    //       accountNumber: form?.chequeAccNo,
    //       accountBranch: form?.chequeAccBank,
    //       chequeNumber: form?.chequeNumber
    //     }
    //   ];
    // } else {
    //   modeInfo = [
    //     {
    //       loanAccountNo: form?.loanAccountNumber,
    //       rePaymentMode: form?.paymentMethod,
    //       // category: this.module,
    //     }
    //   ];
    // }

    // form.modeInfo = modeInfo;
    // form.accountNumber = form?.loanAccountNumber;
    form.transactionAmount = form.transactionAmount
      .toString()
      .replace(/,/g, '');

    if (this.editMode) {
      form.appointmentId = this.routerData.data.appointmentId;
      form.operation = 'update';
    }

    if (this.editMode) {
      this.api.appointmentBooking(form).subscribe((resp) => {
        localStorage.setItem('TransactionTime', resp.transactionTime);
        this.loanRepaymentResponse = resp;
        this.transactionId = this.loanRepaymentResponse.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        if (
          this.loanRepaymentResponse === 200 ||
          this.loanRepaymentResponse !== null
        ) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.onClick(this.loanRepaymentResponse);
          this.form.reset();
        }
      });
    } else {
      form.transactionAmount = form.transactionAmount
        .toString()
        .replace(/,/g, '');
      this.api.cashDepositSave(form).subscribe((resp) => {
        localStorage.setItem('TransactionTime', resp.transactionTime);
        this.loanRepaymentResponse = resp;
        this.transactionId = this.loanRepaymentResponse.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        if (
          this.loanRepaymentResponse === 200 ||
          this.loanRepaymentResponse !== null
        ) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.onClick(this.loanRepaymentResponse);
          this.form.reset();
        }
      });
    }
  }

  goToNextPage() {
    this.flag = false;
  }

  goToPreviousPage() {
    this.flag = true;
  }

  async onClick(event) {
    let modal = await this.modalController.create({
      component: TokenV2Page,
      componentProps: {
        value: event,
        screen: 'onScreen',
      },
    });

    modal.onDidDismiss().then((res) => {
      this.routerData = res;
      console.log('Getting Back Data', this.routerData);
      this.flag = true;
      this.onScreenRefresh();
    });

    return await modal.present();
  }

  goToHomepage() {
    this.router.navigate(['/dashboard']);
  }
}
